// --- File: src/components/steps/ReviewAndGenerateStep.js ---
/**
 * @file ReviewAndGenerateStep.js
 * @description Component for Step 10: Reviewing entered data and generating the XML.
 */
import React, { use } from 'react';
import useReportStore from '../../store/useReportStore';

const ReviewSection = ({ title, data, fields }) => {
    if (!data) return null;

    const renderValue = (value) => {
        if (typeof value === 'boolean') {
            return value ? '' : '';
        }
        if (Array.isArray(value)) {
            if (value.length === 0) return <span className="text-gray-500 italic">  </span>;
            return value.map((item, index) => (
                <span key={index} className="block text-sm text-gray-700 ml-2">- {typeof item === 'object' ? JSON.stringify(item) : item}</span>
            ));
        }
        if (typeof value === 'object' && value !== null) {
            return <pre className="text-xs bg-gray-100 p-1 rounded overflow-x-auto">{JSON.stringify(value, null, 2)}</pre>;
        }
        return value || <span className="text-gray-500 italic"> </span>;
    };

    return (
        <div className="mb-6 p-4 border border-gray-300 rounded-md shadow-sm bg-white">
            <h3 className="text-lg font-semibold text-indigo-700 mb-3 border-b pb-2">{title}</h3>
            <div className="space-y-2">
                {fields.map(field => {
                    let value = data;
                    const pathKeys = field.path.split('.');
                    try {
                        for (const key of pathKeys) {
                            if (value && typeof value === 'object' && key in value) {
                                value = value[key];
                            } else {
                                value = undefined; // Path not found
                                break;
                            }
                        }
                    } catch (e) {
                        value = undefined;
                    }

                    // Special handling for counts of array items
                    if (field.countArray) {
                        value = Array.isArray(value) ? value.length : 0;
                         return (
                            <div key={field.path} className="flex justify-between py-1">
                                <span className="text-sm font-medium text-gray-600">{field.label}:</span>
                                <span className="text-sm text-gray-800 font-semibold">{value}</span>
                            </div>
                        );
                    }
                     // Special handling for specific display
                    if (field.displayFormatter) {
                         return (
                            <div key={field.path} className="py-1">
                                <span className="text-sm font-medium text-gray-600 block mb-1">{field.label}:</span>
                                {field.displayFormatter(value, data)}
                            </div>
                        );
                    }


                    return (
                        <div key={field.path} className="flex justify-between py-1 border-b border-gray-100 last:border-b-0">
                            <span className="text-sm font-medium text-gray-600">{field.label}:</span>
                            <span className="text-sm text-gray-800">{renderValue(value)}</span>
                        </div>
                    );
                })}
            </div>
        </div>
    );
};


const ReviewAndGenerateStep = () => {

    const reportData = useReportStore((state) => state.reportData);

    // Define what to display for each section
    // This is a simplified summary. More detail can be added.
    const generalReportFields = [
        { label: ' ', path: 'reportNumber' },
        { label: '  ', path: 'reportDate' },
        { label: ' ', path: 'reportStatus' }, // Consider mapping code to description
    ];

    const reporterDetailsFields = [
        { label: '  ', path: 'irregularSourceMetaData.sourceName' },
        { label: '  ', path: 'irregularSourceMetaData.sourceId' },
        { label: '  ', path: 'irregularSourceMetaData.reportingPerson.firstName', displayFormatter: (val, data) => `${data?.irregularSourceMetaData?.reportingPerson?.firstName || ''} ${data?.irregularSourceMetaData?.reportingPerson?.lastName || ''}` },
        { label: '  ', path: 'irregularSourceMetaData.reportingPerson.reportingPersonRole' },
    ];

    const eventDetailsFields = [
        { label: ' ', path: 'irregularReportEvent.eventDateTime' },
        { label: '  ', path: 'irregularReportEvent.reportingReasons', displayFormatter: (val) => (Array.isArray(val) && val.length > 0 ? val.map(r => r.reportingReason).join(', ') : ' ')},
        { label: '  ?', path: 'irregularReportEvent.transactionCommitted' },
        { label: ' ', path: 'irregularReportEvent.reportingBriefContent' },
    ];

    const involvedEntitiesFields = [
        { label: '  ', path: 'irregularReportEvent.listOfInvolvedEntities.persons', countArray: true },
        { label: '  ', path: 'irregularReportEvent.listOfInvolvedEntities.corporates', countArray: true },
    ];
     const relatedAccountsFields = [
        { label: '  /', path: 'irregularReportEvent.irregularAccounts', countArray: true },
        { label: '  "/', path: 'irregularReportEvent.irregularOtherAccounts', countArray: true },
    ];
    const pledgesFields = [
        { label: ' ', path: 'irregularReportEvent.irregularPledges', countArray: true },
    ];
    const transactionsFields = [
        { label: ' ', path: 'irregularReportEvent.irregularTransactions', countArray: true },
    ];
     const attachmentsFields = [
        { label: '  (-)', path: 'irregularReportEvent.attachments', countArray: true },
    ];


    return (
        <div className="space-y-6">
            <h2 className="text-2xl font-bold text-gray-800 mb-6"> 10:    XML</h2>
            <p className="text-sm text-gray-600 mb-6">
                     .   ,   '  XML'  .
                <br/>
                 :   .     -XML.
            </p>

            <ReviewSection title="   ( 2)" data={reportData.reportMetaData} fields={generalReportFields} />
            <ReviewSection title="  ( 1)" data={reportData} fields={reporterDetailsFields} />
            <ReviewSection title="    ( 4)" data={reportData} fields={eventDetailsFields} />
            <ReviewSection title="  ( 5)" data={reportData} fields={involvedEntitiesFields} />
            <ReviewSection title="  ( 6)" data={reportData} fields={relatedAccountsFields} />
            <ReviewSection title=" ( 7)" data={reportData} fields={pledgesFields} />
            <ReviewSection title="  ( 8)" data={reportData} fields={transactionsFields} />
            <ReviewSection title=" ( 9)" data={reportData} fields={attachmentsFields} />


            <div className="mt-8 p-4 bg-yellow-50 border border-yellow-300 rounded-md">
                <h4 className="text-md font-semibold text-yellow-800"> :</h4>
                <ul className="list-disc list-inside text-sm text-yellow-700 mt-2">
                    <li>   "  XML",  XML  .</li>
                    <li>      ZIP    `Attachments`    ,   .</li>
                    <li>         .</li>
                </ul>
            </div>
        </div>
    );
};

export default ReviewAndGenerateStep;